import React from "react";
import { useState, useEffect } from "react";
import axios from "axios";
import {
  Panel,
  PanelHeaderSimple,
  FixedLayout,
  Tabs,
  TabsItem,
  Div,
  Spinner,
  HorizontalScroll,
  Group,
  Gallery,
  Header
} from "@vkontakte/vkui";

import { StickersAllItem } from "../items";
import { dataStickersAll } from "../helpers";

const Shop = ({ id, networkStatus, updateConnection, stub, snackbar }) => {
  const [activeTab, setActiveTab] = useState("all-stickers");
  const [slideIndex, setSlideIndex] = useState(1);

  function tab(type) {
    switch (type) {
      case "all-stickers":
        setActiveTab(type);
        window.scrollTo(0, 0);
        break;
      case "game":
        setActiveTab(type);
        window.scrollTo(0, 0);
        break;
      case "anime":
        setActiveTab(type);
        window.scrollTo(0, 0);
        break;
      case "cartoons":
        setActiveTab(type);
        window.scrollTo(0, 0);
        break;
      case "films":
        setActiveTab(type);
        window.scrollTo(0, 0);
        break;
      case "tv-series":
        setActiveTab(type);
        window.scrollTo(0, 0);
        break;
    }
  }

  const renderDataStickersAll = dataStickersAll.map((item, index) => {
    return (
      <StickersAllItem
        key={index}
        id={index + 1}
        name={item.name}
        author={item.author}
        description={item.description}
        price={item.price}
        icon={item.icon}
        amount={item.amount}
      />
    );
  });

  return (
    <Panel id={id} separator={false}>
      <PanelHeaderSimple>VIP Стикеры</PanelHeaderSimple>

      {/* <HorizontalScroll>
          <Div
            style={{
              display: "flex",
              // alignItems: "center",
              // justifyContent: "center",
              // flexDirection: "row",
              marginTop: 20,
              paddingLeft: 10,
              paddingRight: 10,
            }}
          >
         <Div 
            style={{
              backgroundColor: "green",
              borderRadius: "9px",
              height: "140px",
              width: "360px",
              marginRight: 10
            }}
          >

          </Div>
          <Div
            style={{
              backgroundColor: "blue",
              borderRadius: "9px",
              height: "140px",
              width: "360px",
              marginRight: 10
            }}
          ></Div>
          <Div 
            style={{
              backgroundColor: "red",
              borderRadius: "9px",
              height: "140px",
              width: "360px",
              marginRight: 10
            }}
          >

          </Div>
          </Div>
          
        </HorizontalScroll> */}

      <Group header={<Header mode="secondary">Centered</Header>}>
        <Gallery 
        // timeout="1500"
        slideIndex={slideIndex}
        onChange={slideIndex  => setSlideIndex(slideIndex)}
        slideWidth="85%" 
        align="center" 
        style={{ height: 150 }}>
            <Div 
            style={{
              backgroundColor: "green",
              borderRadius: "9px",
              marginRight: "10px"
            }}
          >

          </Div>
          <Div
            style={{
              backgroundColor: "blue",
              borderRadius: "9px",
              marginRight: "10px"
            }}
          ></Div>
          <Div 
            style={{
              backgroundColor: "red",
              borderRadius: "9px",
              marginRight: "10px"
            }}
          >

          </Div>
        </Gallery>
      </Group>

      <>
        <HorizontalScroll>
          <Div
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "space-between",
              flexDirection: "row",
              margin: 10,
              userSelect: "none",
            }}
          >
            <Div
              onClick={() => tab("all-stickers")}
              className={activeTab === "all-stickers" ? "tabGreen" : "tabWhite"}
            >
              Все
            </Div>
            <Div
              onClick={() => tab("anime")}
              className={activeTab === "anime" ? "tabGreen" : "tabWhite"}
            >
              Аниме
            </Div>
            <Div
              onClick={() => tab("game")}
              className={activeTab === "game" ? "tabGreen" : "tabWhite"}
            >
              Игры
            </Div>
            <Div
              onClick={() => tab("cartoons")}
              className={activeTab === "cartoons" ? "tabGreen" : "tabWhite"}
            >
              Мультики
            </Div>
            <Div
              onClick={() => tab("films")}
              className={activeTab === "films" ? "tabGreen" : "tabWhite"}
            >
              Фильмы
            </Div>
            <Div
              onClick={() => tab("tv-series")}
              className={activeTab === "tv-series" ? "tabGreen" : "tabWhite"}
            >
              Сериалы
            </Div>
          </Div>
        </HorizontalScroll>
        {networkStatus ? (
          stub(
            "Отсутствует интернет - соединение!",
            updateConnection,
            "Повторить"
          )
        ) : (
          <>
            <>
              {activeTab === "all-stickers" ? (
                <>
                  <Div>
                    <>
                      {!renderDataStickersAll.length ? (
                        <div
                          style={{
                            display: "flex",
                            alignItems: "center",
                            flexDirection: "column",
                          }}
                        >
                          <Spinner size="large" style={{ marginTop: 90 }} />
                        </div>
                      ) : (
                        <Div
                          style={{
                            paddingLeft: 10,
                            paddingRight: 10,
                          }}
                        >
                          {" "}
                          {renderDataStickersAll}
                        </Div>
                      )}
                    </>
                  </Div>
                </>
              ) : (
                ""
              )}

              {activeTab === "cartoons" ? (
                <>
                  <Div
                    style={{
                      marginTop: 55,
                    }}
                  >
                    {/* <ExchangeSell
                          durkaCoins={formatCoins(durkaCoins)}
                          course={course}
                          addExchangeOffer={addExchangeOffer}
                          onChangeAction={onChangeAction}
                          costOneDC={costOneDC}
                          inputDurkaCoins={inputDurkaCoins}
                          qiwiAccount={qiwiAccount}
                        /> */}
                    ТЕСТ
                  </Div>
                </>
              ) : (
                ""
              )}
            </>

            {snackbar}
          </>
        )}
      </>
    </Panel>
  );
};

export { Shop };
